import { makeStyles } from "@mui/styles";
import { PropsWithChildren } from "react";

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
}), {name: 'flex'});

export const Flex = ({children}: PropsWithChildren) => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      {children}
    </div>
  );
};